import * as React from 'react';
import { HiMenu } from 'react-icons/hi';
import { Pages } from '../../models/pages';

export default function Header(props) {
    const headerItems = Object.values(Pages);

    return <div className={"header"}>
            <div className={"header__button-container"}>
                <HiMenu className={"header__button-icon"}/>
            </div>
            {headerItems.map(item =>
                <div key={headerItems.indexOf(item)} className={"header__item"} onClick={() => props.headerItemClicked(item)}>
                    <div className={"header_item-text"}>{item}</div>
                </div> )}
        </div>
}